import request from '@/utils/request'

export function treeInfo(params) {
    return request({
        url: '/tree/treeInfo',
        method: 'get',
        params
    })
}

export function upgradeInfo(params) {
    return request({
        url: '/tree/upgradeInfo',
        method: 'get',
        params
    })
}

export function upgrade(data) {
    return request({
        url: '/tree/upgrade',
        method: 'post',
        data
    })
}

export function treeTeam(params) {
    return request({
        url: '/tree/treeUser',
        method: 'get',
        params
    })
}

export function treeWater(params) {
    return request({
        url: '/tree/treeLog',
        method: 'get',
        params
    })
}

export function joinTree(data) {
    return request({
        url: '/tree/joinTree',
        method: 'post',
        data
    })
}














