<template>

    <div class="box">
        <navbar></navbar>
        <div class="content" style="background:linear-gradient(90deg,rgba(102,95,238,1),rgba(39,194,222,1));">
            <div class="first-box warp-1200 ov">
                <p class="logo-img"><img src="@/static/images/logoww.png" width="184" height="58" alt=""><img src="@/static/images/wenzi.png" width="669" height="60"  alt=""></p>
                <div class="left fl">
                    <div class="art">
                        <p>水滴还基于区块链技术开发，是一个能</p>
                        <p>让您迅速还完所有负债的众筹还款平台</p>
                    </div>
                    <div class="explain">
                        <p><img src="@/static/images/shuidi.png" width="10" height="14" alt="" style="vertical-align: middle;margin-right: 8px;"><span style="vertical-align: middle;">基于智能合约的众筹还款平台，拯救百万负债难民</span></p>
                        <p><img src="@/static/images/shuidi.png" width="10" height="14" alt="" style="vertical-align: middle;margin-right: 8px;"><span style="vertical-align: middle;">基于智能合约的互助平台，帮助更多的领导人实现财富自由</span></p>
                        <p><img src="@/static/images/shuidi.png" width="10" height="14" alt="" style="vertical-align: middle;margin-right: 8px;"><span style="vertical-align: middle;">使用公平公正的智能合约，打造智能合约界的“轻松筹”</span></p>
                    </div>
                    <div class="down" style="position: relative;">
                        <p @mouseenter="endterAndroid" @mouseleave="leaveAndroid"><img src="@/static/images/index/android.png" alt=""><span>Android下载</span></p>            <div v-show="android" style="position: absolute;left: 60px;top: 98px;z-index: 99;"><img style="width: 150px;height: 150px;" src="@/static/images/download_qrcode.png" alt=""></div>
                        <p @click="downLoad('Ios')"><img src="@/static/images/index/iphone.png" alt=""><span>iPhone下载</span></p>
                    </div>
                </div>
                <div class="right fr"><img src="@/static/images/banner1.png" width="419" height="543" alt=""></div>
            </div>
        </div>
        <div class="content" style="background: #F2F4F9;">
            <div class="new-box3 warp-1200">
                <p class="title">众筹还款模式</p>
                <div class="left">
                    <div class="explain">
                        <p>众筹还款模式，是2019年非常火爆的模式，现在经济下行、物价居高不下，负债已经成为常态，据不完全统计，中国的负债人群高达四亿人左右，负债市场巨大，平均人均负债17万元，信用卡、花呗、借呗、房贷、车贷、银行贷款····压得当代人喘不过气，而本平台旨在使用公正透明的区块链技术，打造一个众筹还款平台，造福百万负债难民。</p>
                        <p>这个模式可以让您迅速还清债务，而且平台安全可靠，平台不掌控资金，一切都运行在公平透明的智能合约上，经过十三个阶段众筹，可以让众多的负债者迅速还完负债，一身轻松。</p>
                    </div>
                </div>
                <div class="right">
                    <img src="@/static/images/xiaoren.png" width="540" height="374"  alt="">
                    <span>三三复制倍增</span>
                </div>
            </div>
        </div>

        <div class="content" style="background: #FFFFFF;">
            <div class="new-box2 warp-1200">
                <p class="title">还款原理</p>
                <div class="left">
                    <img src="@/static/images/index/pc.png" width="570" height="726"  alt="">
                </div>
                <div class="right">
                    <div class="explain">
                        <p style="color: #EDBC13;font-size: 20px;">裂变收益：</p>
                        <p>裂变的收益是巨大的，水滴还平台分为13个等级，推广制度是1推3，参与本平台，每个人只需要交400元（小额版10元），推荐三个负债的朋友加入本平台，然后让三个负债的朋友重复上述内容，在一推三制度的裂变下，很快您会拥有几千人甚至上万人的团队，迅速裂变的团队能让您很快还清负债，并且赚取一大笔钱。</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="content" style="background: #F2F4F9;">
            <div class="new-box1 warp-1200 ov">
                <p class="title">如何参与</p>
                <div class="right" style="text-align: center;">
                    <!--<p><i>1</i><span>交400元</span></p>
                    <p><i>2</i><span>推荐三个负债的朋友加入本平台</span></p>
                    <p><i>3</i><span>及时升级到高级别，不然收不到下级的升级资金</span></p>
                    <p><i>4</i><span>让三个负债的朋友重复上述内容即可</span></p>-->
                    <img style="display: inline-block;" src="@/static/images/index/bg05.png" height="226" width="1158" alt="">
                </div>
                <div style="margin-top: 45px;margin-left: 55px;font-size:14px;font-weight:bold;color:rgba(102,102,102,1);line-height:30px;">
                    <p>注意事项</p>
                    <p>1、交不起400元（小额版10元）的，请谨慎参与</p>
                    <p>2、找不到3个负债朋友的，请谨慎参与</p>
                </div>
            </div>
        </div>
        <div class="content" style="background:linear-gradient(90deg,rgba(102,95,238,1),rgba(39,194,222,1));">
            <div class="second-box warp-1200">
                <p class="title">优势</p>
                <ul>
                    <li><i class="one"></i><span class="bi-tit">安全可靠</span><p>所有的智能合约基于以太坊开发，所有的资金按照合约规则流动，项目方无法掌控资金，更无法跑路</p></li>
                    <li><i class="two"></i><span class="bi-tit">没有泡沫</span><p>动态奖金是100%，永远不会崩盘，无后顾之忧</p></li>
                    <li><i class="three"></i><span class="bi-tit">三三复制</span><p>找到3个负债者加入本平台，即可裂变出大团队</p></li>
                    <li style="margin-right: 0;"><i class="four"></i><span class="bi-tit">市场巨大，容易推广</span><p>目前中国的负债人数高达四亿人，市场巨大，容易推广，而且社区接受程度高，不崩盘不伤人脉</p></li>
                    <li><i class="five"></i><span class="bi-tit">以小博大</span><p>参与门槛低，无风险，仅需400元即可参与，无亏损风险，不需要大资金投入，即可撬动百万资产</p></li>
                    <li><i class="six"></i><span class="bi-tit">几个月即可还清债务</span><p>您升到最高级别第13级后，全力推广，仅需2-5个月即可还清所有债务，一身轻松</p></li>
                    <li><i class="seven"></i><span class="bi-tit">债务实行拆分</span><p>平台把债务拆分成几个还款阶段来进行还款，助力更多负债朋友尽快还清债务</p></li>
                    <li><i class="eight"></i><span class="bi-tit">三方共赢</span><p>实现负债者，推广者，平台三方共赢</p></li>
                </ul>
            </div>
        </div>
        <div class="content" style="background:rgba(255,255,255,1);" id="mao">
            <div class="three-box warp-1200">
                <!--<p class="title">官方财富树</p>-->
                <div class="tag-t"><p><span :class="{act:hotTree == 1}" @click="hotTree = 1">水滴还正式版<template v-if="joinstate_1">/已参与</template><template v-else>/未参与</template></span><span :class="{act:hotTree == 2}" @click="hotTree = 2">水滴还小额版 <template v-if="joinstate_2">/已参与</template><template v-else>/未参与</template></span></p></div>
                 <treebox v-show="hotTree == 1" :tree_id="1" @listenJoinState="changeOneState"></treebox>
                 <treebox v-show="hotTree == 2" :tree_id="2" @listenJoinState="changeTwoState"></treebox>
            </div>
        </div>
        <div class="content" style="background:linear-gradient(90deg,rgba(102,95,238,1),rgba(39,194,222,1));">
            <div class="five-box warp-1200 ov">
                <p class="title">意见反馈</p>
                <div class="left fl">
                    <img src="@/static/images/fankui.png" width="398" height="384" alt="" style="margin-top: 30px;">
                </div>
                <div class="right fr pr">
                    <div class="iphorm-input">
                        <textarea v-model="optionFrom.content"  maxlength="100" placeholder="请输入意见反馈" class="element-textarea"></textarea>
                        <input v-model="optionFrom.email" type="text" placeholder="请填写邮箱">
                        <button :disabled="optionState" @click="submiOption">提交</button>
                    </div>
                    <p class="toast" v-if="toastState">{{message}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar } from '@/components'

    import {option} from "../../api";
    import {isEmail} from "../../utils/validate";
    import {treebox} from "./components"
    export default {
        name: "Index" ,
        components: {
            Navbar,
            treebox
        },
        data()
        {
            return {
                joinstate_1:false,
                joinstate_2:false,
                hotTree:1, //选中财富树类型
                toastState:false,
                message:'',
                optionState:false,
                optionFrom:{
                    content:'',
                    email:''
                },
                android: false,
                genuineTree:{},//正式版财富树
                experienceTree:{}//体验版财富树

            }
        },
        watch:{

        },
        mounted()
        {


        },
        methods:{
            endterAndroid()
            {
                this.android = true
            },
            leaveAndroid()
            {
                this.android = false
            },
            toast(message)
            {
                this.message = message
                this.toastState = true;
                let that = this
                setTimeout(function () {
                    that.toastState = false
                }, 1.5 * 1000)
            },
            // 提交表单
            submiOption()
            {
                if (this.optionFrom.content == '' || this.optionFrom.content.length < 20)
                {
                    this.toast('反馈内容最少20个字符')
                    return
                }
                if (this.optionFrom.email == '' || !this.optionFrom.email)
                {
                    this.toast('请填写邮箱')
                    return
                }
                if (!isEmail(this.optionFrom.email))
                {
                    this.toast('请邮箱格式不正确')
                    return
                }
               this.optionState = true
                option(this.optionFrom).then(res => {
                    this.optionState = false
                    if (res.error)
                    {
                        this.toast(res.msg)

                    } else
                    {
                        this.optionFrom.content = ''
                        this.optionFrom.email = ''
                        this.toast('意见反馈发送成功')
                    }

                })
            },
            //修改加入状态
            changeOneState(state)
            {
                this.joinstate_1 = state
            },
            changeTwoState(state)
            {
                this.joinstate_2 = state
            },
            // 下载
            downLoad(type)
            {
                if (type == 'Ios')
                {
                    this.$confirm('苹果手机用户请使用浏览器访问本网址', '系统提醒', {
                        showCancelButton:false,
                        type: 'success'
                    }).then(() => {

                    })
                    return
                }
                this.$confirm('努力研发中...', '系统提醒', {
                    showCancelButton:false,
                    type: 'success'
                }).then(() => {

                })
            }


        }
    }
</script>

<style scoped>
     @import "../../static/css/index.css";
     .toast{
         position: absolute;
         width: 240px;
         height: 40px;
         background: #333333ad;
         text-align: center;
         line-height: 40px;
         color: #fff;
         font-size: 14px;
         top: 44%;
         margin-top: -20px;
         left: 50%;
         margin-left: -120px;
         border-radius: 6px;

     }
     .tag-t{
         margin-top: 88px;
     }

</style>



