<template>
    <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeIn"
    >
        <div>
            <div>
                <div class="left">
                    <p class="icon"><i></i></p>
                    <p class="framework">架构</p>
                    <div class="profit ov">
                        <div class="expend fl">
                            <p class="tit">您的支出</p>
                            <p class="total">往上1-{{treeInfo.level}}层付款{{treeInfo.level}}次，总花费{{treeInfo.upTotalMoney_rmb}}元</p>
                            <p class="top" v-for="(item, index) in treeInfo.upgrade_money_rmb">升{{index+1}}级：给上面第{{index+1}}级{{item}}元</p>
                        </div>
                        <div class="income fl">
                            <p class="tit">您的收入</p>
                            <p class="total">往下1-{{treeInfo.level}}层收款{{treeInfo.lowTotalCount}}次，总收款{{treeInfo.lowTotalMoney_rmb}}元</p>

                            <p class="top" v-for="(item, index) in treeInfo.low_money_rmb">下{{index+1}}级收取：{{item.money}}元×{{item.count}}人={{item.total}}元</p>
                        </div>
                    </div>
                </div>
                <div class="right">
                    <div class="ov level"><img class="fl" src="@/static/images/cengji.png" width="55" height="48" alt=""><p class="level-text fl"><span class="l-text1">层级</span><span class="l-text2">{{treeInfo.level}}级</span></p></div>
                    <div class="ov level"><img class="fl" src="@/static/images/zhidu.png" width="45" height="48" alt=""><p class="level-text fl"><span class="l-text1">推广制度</span><span class="l-text2">一推{{treeInfo.limit}}</span></p></div>
                    <p class="text">建议您同时参与水滴还正式版和小额版，如果您只参与了小额版，而您的下级参与了正式版，那么您的下级参与正式版的收益就流失了。</p>
                    <button class="join" @click="joinConfirm">立即加入<!--<i class="join-icon"></i>--></button>
                </div>
            </div>
            <div v-if="name" class="four-box warp-1200 ov">
                <div class="f-box fl">
                    <p class="icon1"><i></i></p>
                    <p class="tit">升级</p>
                    <p class="grade">您当前的级别：{{upgradeInfo.vip_level}}级</p>
                    <p class="grade">升级到{{tolevel}} 级需要支付{{upgradMoney_rmb}}元({{upgradMoney}} USDT)</p>
                    <p class="balance">USDT余额</p>
                    <p class="num">{{upgradeInfo.money}}</p>
                    <p class="online"></p>
                    <p class="up-to">升级到</p>
                    <div style="text-align: center;">
                        <div class="state-box ov">
                            <p class="fl pr">
                                <span class="line"></span>
                                <span class="vg1">v{{upgradeInfo.vip_level}}</span>
                                <span class="vg2">v{{tolevel}}</span>
                            </p>
                            <i class="fl" @click="addLevel"></i>
                        </div>
                    </div>
                    <p class="upgrade"><button @click="upgradeConfirm">立即升级</button></p>
                </div>
                <div class="f-box fl">
                    <p class="icon2"><i></i></p>
                    <p class="tit">团队</p>
                    <p class="grade">您的下级会员共计：{{teamData.user_count}}人</p>
                    <p class="exp">这里的一级会员指的是“等级”尚在一级的会员。</p>
                    <p class="exp">例如：A升级到2级，那么A就会变动到您的二级会员之内。 以此类推，所以请及时提醒等级较低的用户，完成升级任务。</p>
                    <p class="online" style="margin-bottom: 20px;"></p>
                    <ul class="shrink">
                        <li v-for="(item, index) in teamData.users" @click="teamkey == index ? teamkey = 0 :  teamkey = index">
                            <div class="list-member" ><i></i><a href="javascript:void(0);">{{index}}级会员（{{item.length}}人）</a><span></span></div>
                            <div class="con-list" v-show="teamkey == index">
                                <div  v-if="item.length">
                                    <p class="con-title"><span>手机号</span><span class="qrcode">微信二维码</span></p>
                                    <div class="body-list">
                                        <p v-for="(uitem, uindex) in item"><span>{{uitem.username}}</span><img :src="uitem.wechat_code" style="width: 50px;height: 50px;background: #333;display: inline-block;" alt=""></p>
                                    </div>
                                </div>
                                <div class="body-list" v-else>
                                    <p ><span>暂无用户</span></p>
                                </div>

                            </div>
                        </li>
                    </ul>
                </div>
                <div class="f-box fl">
                    <p class="icon3"><i></i></p>
                    <p class="tit">财务</p>
                    <p class="total-pay ov"><span class="fl t-pay">总共支付升级费用</span><span class="fr t-text">{{treeFinance.head['expense']}}</span></p>
                    <p class="total-pay ov"><span class="fl t-pay">总共收取下级升级费用</span><span class="fr t-text">{{treeFinance.head['income']}}</span></p>
                    <div class="record">
                        <p><img src="@/static/images/caiwulius.png" width="21" height="21" alt=""><span>财务流水记录</span></p>
                        <div class="record-list">
                            <div v-for="item in treeFinance.list" class="record-p"><span>{{item.userinfo.nickname}}</span><span>{{item.datetime}}</span><span>{{item.money}}</span></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import { mapGetters } from 'vuex'
    import fmath from "@/utils/math"
    import {treeInfo, upgradeInfo, upgrade, treeTeam, treeWater, joinTree} from "@/api/tree";
    export default {
        name: "treeInfo",
        props: ['tree_id'],
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        data(){
            return {
                treeInfo:{},
                upgradeInfo:{},
                tolevel:0,
                upgradMoney:0,
                upgradMoney_rmb:0,
                joinState:false,
                teamData:{
                    user_count:0,
                    users:[]
                },
                teamkey:0,
                treeFinance:{
                    head:{
                        expense:0,
                        income:0
                    },
                    list:[]
                },
                pageWater:1

            }
        },
        watch:{
            tolevel(val, old)
            {
                this.getUpMoney(val)
            }
        },
        mounted()
        {
             this.getTreeInfo()

        },
        methods:{

            getTreeInit()
            {
                this.pageWater = 1
                this.getUpgradeInfo()
                this.getTreeTeam()
                this.getTreeWater()
            },
            // 获取财富树信息
            getTreeInfo()
            {
                treeInfo({tree_id:this.tree_id}).then(res => {
                    this.treeInfo = res
                    this.$emit('listenJoinState', res.join_state)
                    if (this.name)
                    {
                        this.getTreeInit()
                    }

                })
            },
            // 获取升级信息
            getUpgradeInfo()
            {
                  upgradeInfo({tree_id:this.tree_id}).then(res => {
                        this.upgradeInfo = res
                        this.tolevel = res.vip_level + 1
                  })

            },
            // 获取团队成员
            getTreeTeam()
            {
                treeTeam({tree_id:this.tree_id}).then(res => {
                    this.teamData = res
                })
            },

            // 获取财富树流水
            getTreeWater()
            {
                if (this.name)
                {
                    treeWater({tree_id:this.tree_id, page:this.pageWater}).then(res => {
                        this.treeFinance = res
                    })
                }
            },
            //加入确认
            joinConfirm()
            {
                this.$confirm('加入水滴还，将从您的账户里面扣除'+this.treeInfo.money_rmb+'元('+this.treeInfo.money+'个USDT)，作为一级的支付费用', '系统提醒', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.joinTree()
                }).catch(() => {

                });
            },
            // 加入财富树
            joinTree()
            {
                joinTree({tree_id:this.tree_id}).then(res => {

                    if (res.error == 1)
                    {
                        this.$confirm('余额不足, 请立即充值', '系统提醒', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'error'
                        }).then(() => {
                            this.$router.push('/center')
                        }).catch(() => {


                        });
                        return
                    }
                    this.$confirm('加入成功!', '系统提醒', {
                        showCancelButton:false,
                        type: 'success'
                    }).then(() => {
                        this.$emit('listenJoinState', true)
                        this.getTreeInit()

                    })
                })
            },

            // 增加等级
            addLevel()
            {
                if (this.tolevel + 1 <= this.treeInfo.level)
                {
                    this.tolevel++
                }


            },
            // 获取升级金额
            getUpMoney(tolevel)
            {

                // 计算升级价格
                let upmoney = 0
                let upmoney_rmb = 0
                for (let i = 0; i < tolevel; i++)
                {
                    if (i < this.upgradeInfo.vip_level)
                    {
                        continue
                    }

                    upmoney = fmath.floatAdd(upmoney, this.treeInfo.upgrade_money[i])
                    upmoney_rmb = fmath.floatAdd(upmoney_rmb, this.treeInfo.upgrade_money_rmb[i])
                }

                this.upgradMoney = upmoney
                this.upgradMoney_rmb = upmoney_rmb
            },
            // 升级确认
            upgradeConfirm()
            {
                if (this.upgradeInfo.vip_level == 0)
                {
                    this.$confirm('请先加入水滴还!', '系统提醒', {
                        showCancelButton:false,
                        type: 'error'
                    }).then(() => {
                        document.documentElement.scrollTop=4600;//不需要加单位
                    })
                    return
                }

                this.$confirm('升级到'+this.tolevel+'需要支付'+this.upgradMoney_rmb+'元('+this.upgradMoney+'USDT), 确定升级么?', '系统提醒', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.upgradeLevel();
                }).catch(() => {


                });


            },
            // 升级
            upgradeLevel()
            {
                upgrade({tree_id:this.tree_id, tolevel:this.tolevel}).then(res => {

                    this.upgradeInfo.vip_level = this.tolevel
                    this.tolevel = this.tolevel + 1 <= 9 ? this.tolevel + 1 : 9
                    this.upgradeInfo.money = res.money
                    this.$confirm('升级成功!', '系统提醒', {
                        showCancelButton:false,
                        type: 'success'
                    }).then(() => {

                    })
                })
            }

        }
    }
</script>

<style scoped>
     .left{width: 780px;float: left;background:#BEDEF4;box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.1);border-radius:6px;padding-bottom: 30px}
     .left .icon{text-align: center;}
     .left .icon i{display: inline-block;width: 54px;height: 48px;padding: 22px 14px;background: url(../../../static/images/jiagou.png) no-repeat center;background-size: 54px 48px;}
     .left .framework{text-align: center;font-size:34px;font-weight:bold;color:#222222;margin-bottom: 30px;}
     .left .profit .expend{width: 390px;border-right: 1px solid #bec6d8;}
     .left .profit .expend .tit{text-align: center;color: #333333;font-size:24px;font-weight:bold;}
     .left .profit .expend .total{font-size:14px;font-weight:bold;color:rgba(4,174,229,1);margin-top: 22px;margin-bottom: 28px;padding-left: 26px;}
     .left .profit .expend .top{font-size:14px;font-weight:bold;color:#666666;padding-bottom: 16px;margin-left: 46px;}
     .left .profit .income{width: 380px;}
     .left .profit .income .tit{text-align: center;color: #333333;font-size:24px;font-weight:bold;}
     .left .profit .income .total{font-size:14px;font-weight:bold;color:rgba(4,174,229,1);margin-top: 22px;margin-bottom: 28px;padding-left: 26px;}
     .left .profit .income .top{font-size:14px;font-weight:bold;color:#666666;padding-bottom: 16px;margin-left: 46px;}
     .right{margin-left: 100px;float: left;margin-top: 70px;width: 319px;}
     .right .text{line-height: 22px;font-size: 16px;color: #666;}
     .right .level{margin-bottom: 80px;}
     .right .level img{vertical-align: middle;}
     .right .level .level-text{margin-left: 20px;line-height: 48px;}
     .right .level .level-text .l-text1{font-size:34px;font-weight:bold;color: #222;}
     .right .level .level-text .l-text2{font-size:28px;font-weight:bold;color: #222;margin-left: 30px;display: inline-block;}
     .right .join{width:158px;height:46px;background:rgba(10,110,242,1);border:1px solid rgba(19,133,255,1);border-radius:23px;margin-top: 80px;color: #fff;cursor: pointer;font-size: 18px;}
     .right .join .join-icon{display: inline-block;width: 15px;height: 7px;background: url(../../../static/images/youjiantou.png) no-repeat center;background-size: 15px 7px;margin-left: 10px;}


      .four-box{padding-top: 200px;overflow: hidden;}
      .four-box .f-box{width:328px;height:600px;background:#BEDEF4;border-radius:6px;box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.1);margin-right: 20px;padding: 0 29px;}
      .four-box .f-box:last-child{margin-right: 0;}
      .four-box .f-box .icon1{text-align: center;}
      .four-box .f-box .icon2{text-align: center;}
      .four-box .f-box .icon3{text-align: center;}
      .four-box .f-box .icon1 i{display: inline-block;width: 60px;height: 48px;background-size: 54px 48px;background: url(../../../static/images/shegnji.png) no-repeat center;margin: 42px 0 14px;}
      .four-box .f-box .icon2 i{display: inline-block;width: 60px;height: 48px;background-size: 60px 48px;background: url(../../../static/images/tuandui.png) no-repeat center;margin: 42px 0 14px;}
      .four-box .f-box .icon3 i{display: inline-block;width: 60px;height: 48px;background-size: 42px 48px;background: url(../../../static/images/caiwu.png) no-repeat center;margin: 42px 0 14px;}
      .four-box .f-box .tit{font-size:34px;text-align: center;font-weight:bold;color:#222222;margin-bottom: 30px;}
      .four-box .f-box .grade{text-align: center;font-size:14px;font-weight:bold;color:rgba(4,181,236,1);margin-bottom: 28px;}
      .four-box .f-box .balance{text-align: center;font-weight:bold;color:#333333;font-size:24px;}
      .four-box .f-box .num{color: #444;font-size:14px;font-weight:bold;text-align: center;margin-top: 20px;}
      .four-box .f-box .online{border-bottom: 1px solid #bec6d8;margin-top: 20px;}
      .four-box .f-box .up-to{color: #FE586C;font-size:12px;font-weight:bold;text-align: center;margin-top: 50px;}
      .four-box .f-box .state-box{display: inline-block;height: 30px;padding-top: 6px;}
      .four-box .f-box .state-box p .line{width: 160px;height: 2px;display: block;background: #FE586C;margin-top: 7px;}
      .four-box .f-box .state-box p .vg1 {position: absolute;left: 0;top: 0;font-size: .12rem;color: #FFFFFF;background: #FE586C;padding: 2px 6px;border-radius: 10px;}
      .four-box .f-box .state-box p .vg2 {position: absolute;right: 0;top: 0;font-size: .12rem;color: #FFFFFF;background: #FE586C;padding: 2px 6px;border-radius: 10px;}
      .four-box .f-box .state-box i {width: 30px;height: 30px;display: block;background: url(../../../static/images/add.png) no-repeat center;background-size: 24px;margin-left: 10px;margin-top: -6px;cursor: pointer;}
      .four-box .f-box .upgrade{text-align: center;margin-top: 60px;}
      .four-box .f-box .upgrade button{background:rgba(10,110,242,1);border:1px solid rgba(19,133,255,1);border-radius: 28px;color: #fff;cursor: pointer;font-size: 18px;padding: 10px 32px}
      .four-box .f-box .exp{font-size:14px;font-weight:bold;color:#666666;line-height: 16px;}
      .four-box .f-box .shrink{height: 250px;overflow: auto;}
      .four-box .f-box .shrink li .list-member{height: 32px;line-height: 32px;}
      .four-box .f-box .shrink li .list-member i {width: 17px;height: 16px;background: url(../../../static/images/huiyuan.png) no-repeat center;background-size: 100%;display: inline-block;vertical-align: middle;margin-right: 10px;}
      .four-box .f-box .shrink li .list-member a {font-size: 14px;color: #666666;vertical-align: middle;}
      .four-box .f-box .shrink li .list-member span {width: 14px;height: 8px;background: url(../../../static/images/zhankai.png) no-repeat center;background-size: 100%;display: inline-block;float: right;margin-top: 14px;}
      .four-box .f-box .shrink li .con-list{line-height: 54px;}
      .four-box .f-box .shrink li .con-title{font-size: 12px;color: #666666;height: 30px;line-height: 30px;}
      .four-box .f-box .shrink li .con-title span{margin-left: 30px;}
      .four-box .f-box .shrink li .body-list{font-size: 12px;color: #666666;}
      .four-box .f-box .shrink li .body-list p span{margin-left: 30px;}
      .four-box .f-box .shrink li .body-list p img{margin-left: 60px;}
      .four-box .f-box .shrink li .con-title .qrcode{margin-left: 80px;}
      .four-box .f-box .total-pay{margin-bottom: 20px;}
      .four-box .f-box .total-pay .t-pay{font-size:18px;font-weight:bold;color:#666666;line-height: 18px;}
      .four-box .f-box .total-pay .t-text{font-size:14px;font-weight:bold;color:rgba(4,181,236,1);line-height: 18px;}
      .four-box .f-box .record p{margin-bottom: 20px;}
      .four-box .f-box .record p img{vertical-align: middle;margin-right: 10px;}
      .four-box .f-box .record p span{vertical-align: middle;font-size:18px;color:rgba(243,152,0,1);font-weight:bold;}
      .four-box .f-box .record .record-list{height: 284px;overflow: auto;}
      .four-box .f-box .record .record-list .record-p{font-size:14px;color:#666666;margin-bottom: 16px;}
      .four-box .f-box .record .record-list .record-p span{width: 33%;display: inline-block;text-align: center;}

</style>



